@font-face {
    font-family: Noirden;
    font-weight: bold;
    src: url('../assets/fonts/Noirden/Noirden-Bold.ttf');
}

@font-face {
    font-family: Noirden;
    font-weight: 600;
    src: url('../assets/fonts/Noirden/Noirden-SemiBold.ttf');
}

@font-face {
    font-family: Noirden;
    src: url('../assets/fonts/Noirden/Noirden-Regular.ttf');
}

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');


* {
    margin: 0;
    padding: 0;
    font-family: 'Poppins';

}


.hero-section {
    height: 100vh;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}

.navigation-bar {
    // background-color: red;
    display: flex;
    width: 90%;
    padding: 29px;
    height: 100px;
    max-width: 1900px;
    justify-content: space-between;
    align-items: center;

    button{
        display: none;
    }

    .navigaton-bar-logo {
        width: 1em;
    }
   

    .navigation-bar-buttons {
        .navbar-first-button {
            margin-right: 64px;
        }
    }
}

.hero-business{
    margin: 0px 165px;
    margin-top: 155px;
    max-width: 1900px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin-bottom: 100px;

    .hero-left {
        max-width: 1000px;
        margin: 0px 75px 0px 0px;

        .main-text {
            color: #2F2F2F;
            font-family: 'Poppins';
            font-size: 55px;
            font-style: normal;
            font-weight: 700;
            line-height: normal;
            text-transform: uppercase;
            text-align: left;
            margin: 0px 0px 40px 0px;

            .solid {
                background-color: #2F2F2F;
                padding: 0px 10px;
                border-radius: 10px;
                color: white;
                margin: 10px;
            }

        }

        .hero-paragraph {
            color: #2F2F2F;
            font-family: Noirden;
            font-size: 36px;
            font-style: normal;
            text-align: left;
            font-weight: 400;
            line-height: 54px;
            /* 150% */
            letter-spacing: 1.08px;
        }

        .hero-buttons {
            display: flex;
            margin-top: 40px;

            .hero-left-button {
                margin-right: 34px;
            }
        }
    }

    .hero-right {
        img {
            width: 25vw;
            max-width: 550px;
        }
    }
}


.brands-section {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    .brands-section-images {
        max-width: 1900px;
        width: 90vw;
        display: flex;
        justify-content: space-around;
        align-items: center;

        img {
            height: 100px;
        }
    }
}

.customer-relationship-section {
    height: 100vh;
    // width: 100vw;
    // margin: auto;
    max-width: 1900px;
    background-image: url('../assets/svg/background_rectangle.svg');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 60px 0px;


    .customer-relationship-section-cards {
        display: flex;
    }
}

.relationship-card {
    width: 18vw;
    max-width: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 40px 28px;
    border-radius: 20px;
    border: 4px solid #FFF;
    background: #F8F9FC;
    margin: 0px 25px;
    box-shadow: 0px 4px 40px 0px rgba(248, 249, 252, 0.07);

    img {
        height: 15vw;
        max-height: 300px;
        margin-bottom: 64px;
    }

    .relationship-card-content {
        width: 100%;

        .relationship-card-title {
            margin-bottom: 24px;
        }

        .relationship-card-description {
            color: #929696;
            font-family: Poppins;
            font-size: 20px;
            text-align: center;
            font-style: normal;
            font-weight: 500;
            line-height: normal;
            letter-spacing: -0.2px;
        }
    }
}

.business-partner-review-section {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 0px 165px;
    margin-bottom: 60px;

    .business-partner-review-section-comments {
        width: 100%;
        display: flex;
        justify-content: center;

        // align-items: center;
        .business-partner-review-section-comments-card-continer {
            margin: 0px 25px;
        }
    }
}

.partner-comment-card-name-section {
    display: flex;
    align-items: center;
    position: relative;
    top: -30px;

    .partner-comment-card-image {
        margin-right: 20px;
        margin-left: 32px;

        img {

            width: 90px;
        }
    }

    .partner-comment-card-name {
        color: #2F2F2F;
        font-family: Poppins;
        font-size: 20px;
        text-align: left;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
    }
}

.partner-comment-card-quote {
    position: relative;
    bottom: -20px;
    left: 20vw;
    width: min-content;
}

.partner-comment-card {
    width: 20vw;
    border-radius: 20px;
    background: #F8F9FC;
    padding: 28px 36px 58px 28px;

    .partner-comment {
        color: #929696;
        font-family: Poppins;
        font-size: 20px;
        text-align: left;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        letter-spacing: -0.2px;
    }

}


.common-questions-asked-section {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100vw;

    .common-questions-asked-section-title {
        text-align: center;
    }





}


.question-box {

    margin: 24px 10px;
    width: 80vw;
    max-width: 1900px;
    border-radius: 12px;
    background: #F8F9FC;
    padding: 29px 48px;

    .question-box-top {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .question-box-question {
            color: #2F2F2F;
            font-family: Poppins;
            font-size: 28px;
            font-style: normal;
            text-align: left;
            font-weight: 500;
            line-height: normal;
        }

        .icon {
            background-color: white;
            padding: 10px;
            width: 34px;
            height: 34px;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;

            img {
                width: 24px;
            }
        }


    }

    .question-box-answer {
        margin-top: 40px;
        margin-right: 100px;
        color: #2F2F2F;
        font-family: Poppins;
        font-size: 24px;
        font-style: normal;
        text-align: left;
        font-weight: 400;
        line-height: normal;
        letter-spacing: -0.24px;
    }

}



.talk-us-section {

    background-image: url("../assets/svg/orange_rectangle.svg");
    width: 90vw;
    height: 500px;
    background-repeat: no-repeat;
    background-size: contain;
    margin: auto;
    margin-top: 120px;
    margin-bottom: 150px;
    display: flex;
    justify-content: center;
    align-items: center;

    .talk-us-section-text {
        max-width: 750px;
        text-align: left;
        color: #FFF;
        font-family: Poppins;
        font-size: 48px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
    }

    .talk-us-section-button {

        a {
            padding: 20px 48px;
            border: 2px solid #FFF;
            border-radius: 40px;
            text-decoration: none;
            color: #FFF;
            font-family: Poppins;
            font-size: 24px;
            font-style: normal;
            font-weight: 600;
            line-height: 32px;
            /* 133.333% */
        }
    }
}


.footer-section {
    background-color: #000;
    color: #F9F9FF;
    padding: 80px 165px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .footer-section-left {
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
        color: #F9F9FF;
        font-family: Noirden;
        font-size: 32px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;

        .footer-section-left-top {
            margin-bottom: 58px;

            a {
                margin: 0px 40px;
                text-decoration: none;
                color: #F9F9FF;
            }

            :first-child {
                margin-left: 0px;
            }
        }

    }

    .footer-section-right {

        .footer-section-right-top {

            margin-bottom: 32px;

        }

        .footer-section-right-bottom {
            display: flex;
            justify-content: center;
            align-items: center;

            span {
                color: #F9F9FF;
                font-family: Poppins;
                font-size: 32px;
                font-style: normal;
                font-weight: 400;
                line-height: normal;
            }

            a {
                margin: 0px 12px;
            }
        }
    }
}


.running-program-section {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-bottom: 170px;

    .running-program-direct-image {
        width: 80vw;
        max-width: 1500px;
    }

    .running-programm-mobile-card{
        display: none;
    }

}


// Utils

.solid-button {
    color: #FFF;
    font-size: 24px;
    font-family: 'Poppins';
    text-align: left;
    font-weight: 600;
    text-decoration: none;
    background-color: #2F2F2F;
    padding: 1em 2em;
    border-radius: 100px;

}

.button {
    color: #000;
    text-align: center;
    font-size: 32px;
    font-style: normal;
    text-decoration: none;
    font-weight: 400;
    line-height: 54px;
    letter-spacing: 0.96px;
}

.button-with-border {
    color: #2f2f2f;
    font-size: 24px;
    font-family: 'Poppins';
    font-weight: 600;
    text-decoration: none;
    background-color: #ffffff;
    border: 2px solid #2f2f2f;
    padding: 20px 48px;
    border-radius: 100px;
}

.heading {
    margin-bottom: 60px;
    color: #2F2F2F;
    font-family: Poppins;
    font-size: 48px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}


.heading-2 {
    color: #2F2F2F;
    font-family: Poppins;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 0.48px;
}





