.center-section {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}


body {
    width: 100%;
    overflow-x: hidden;
    margin: 0;
    padding: 0;

}


.rotated {
    transform: rotate(180deg);
}


.reversed {
    display: flex;
    flex-direction: column-reverse;
}

.flipped-horizontal {
    transform: scaleX(-1);
}