.mobile {
    display: none;
}


.tablet {
    display: none;
}


* {
    scroll-behavior: smooth;
}

body {
    margin: 0;
    padding: 0;
    width: 100vw;
    height: 100vh;
    overflow-x: hidden;
    font-family: "Noirden";
}


.pnt {
    margin: 1em;
}

.frame {
    height: 100vh;
    width: 100%;
    max-width: 100%;
    scroll-snap-align: start;
}

.snapbottom {
    scroll-snap-align: end;
}

.App {
    scroll-snap-type: y mandatory;
    overflow-y: scroll;
    height: 100vh;
}

.hero {
    overflow-x: hidden;
    overflow-y: hidden;
}

.glimpse-one {
    font-family: Noirden;
