    justify-content: center;
    align-items: center;
    flex-direction: column;

    p {
        max-width: 1500px;
        font-size: 2em;
        width: 35vw;
        color: #5F5F5F;
        font-weight: bold;
    }

    .p-top {
        margin-bottom: 10vh;
        width: 90vw;
        font-size: 1.7em;
        font-weight: 600;
    }

    .h3 {
        font-size: 2em;
        line-height: 1em;
        color: black;
        max-width: 1500px;
        font-weight: bold;
    }

    .glimpse-one-images {
        display: flex;
        align-items: center;
    }

    .glimpse-one-image {
        width: 9vw;
        margin: 0em 1vw;
    }

    .glimpse-one-app {
        width: 12vw;
        margin: 0em 2vw;
    }
}



.working {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    background: url(./assets/working/back.png);
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
}

.working-slider {
    display: flex;
    max-width: 900px;
    width: 90vw;
    height: 100%;
    margin-left: 10vw;
    justify-content: center;

    .working-slider-image {
        width: 50vw;
        position: sticky;
        top: 0px;
        height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            height: 50vh;
            position: absolute;
            right: 0vw;
        }

        .congratsGif {
            height: 250px;
            margin-top: -150px;
            margin-right: 30px;
        }
    }

    .working-slider-text {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 40vw;
        margin-left: 10vw;
        text-align: start;
        flex-direction: column;

        .working-slider-text-heading {
            font-size: 2em;
            font-family: 'Noirden';
            font-weight: bold;
            color: black;
            margin-bottom: .7em;
        }

        .working-slider-text-paragraph {
            width: 40vw;
            font-size: 1.6em;
            text-align: left;
            font-weight: 600;
            font-family: 'Noirden';
            line-height: 1.2em;
            font-weight: 600;
            color: #303030;
            margin-bottom: .5em;
        }

        .working-slider-text-sub-paragraph {
            width: 40vw;
            font-size: 1.5em;
            text-align: start;
            font-family: 'Noirden';
            margin-top: 2em;
            margin-bottom: 0px;
            color: #303030;
        }
    }
}

.working-heading {
    font-size: 2.5em;
    font-family: 'Noirden';
    font-weight: bold;
    background: url(./assets/working/back.png);
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    position: sticky;
    top: 0;
    padding-block: 1em;
    width: 100%;
    background-color: white;
    z-index: 2;
}

.invisible {
    opacity: 0;
    transition: opacity .2s linear;
}

.visible {
    opacity: 1;
    transition: opacity .2s linear;
}


