.hero-part {
    background: url('./assets/hero/hero-back.svg');
    background-position: center top;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: calc(100% - 135px);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .main-text {
        display: flex;
        flex-wrap: wrap;
        margin-top: -100px;
        justify-content: center;
        align-items: center;
        width: 70vw;
        max-width: 1000px;
        margin-bottom: 70px;
        font-size: 100px;
        font-variant-caps: all-petite-caps;

        span {
            margin-block-end: 0px;
            margin: 1px;
            padding-block-start: 0px;
            font-weight: 700;
            line-height: 70px
        }

        span::after {
            content: " ";
            display: inline-block;
            margin-top: 0em;
        }

        span::before {
            content: " ";
            display: inline-block;
            margin-top: 0em;
        }

        .diff-color {
            color: #CE8201
        }

        .storebuttons {
            margin-top: 4em;
        }


    }

}


.coins-left-1 {
    animation-delay: 2s;
}

.coins-left-2 {
    animation-delay: 0s;
}

.coins-left {
    position: relative;
    top: -50vh;
    left: -30vw;
    width: 200px;
    z-index: 100;
    animation: floating;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
    animation-direction: alternate;
    animation-play-state: running;
}



.arrow-image {
    width: 70px;
    position: relative;
    left: 18vw;
    margin-bottom: -50px;
    margin-top: -60px;
    z-index: 0;
}

.arrow-down {
    position: relative;
    bottom: 50px;
    width: 30px;
    animation: floating;
    animation-delay: 1s;
    animation-timing-function: ease-in-out;
    animation-duration: 2s;
    animation-iteration-count: infinite;
}


.panel {

    position: absolute;
    right: -40vw;
    top: 0;
    width: 20vw;
    height: 100%;
    font-size: 1.8em;
    text-align: left;
    padding: 1em;
    padding-top: 1.6em;
    padding-inline: 2em;
    display: none;
    z-index: 3;
    background-color: rgb(240, 240, 211);
    box-shadow: -5px 0px 10px -8px grey;

    div {
        margin-bottom: 1em;

        a {
            text-decoration: none;
            color: black;
        }
    }

    .close-button {
        display: flex;
        width: 95%;
        justify-content: right;
        margin-bottom: 2em;

        // margin-left: 1em;
        button {
            background: none;
            border: none;

            img {
                width: 30px;
            }
        }
    }

}

.panel-enter-done {
    right: 0;
    transition: .3s;
    display: block;

}

.panel-enter-active {
    right: -40vw;
    display: block;
}

.panel-exit-done {
    right: -40vw;
    display: none;
    transition: .3s;
}


.panel-exit-active {
    right: 0;
    display: block;
}







