}



.coin1 {
    position: relative;
    top: -50vh;
    left: -30vw;
    width: 50px;
    animation-name: floating, blur;
    animation-delay: 0;
    animation-timing-function: ease-in-out;
    animation-duration: 4s;
    animation-iteration-count: infinite;

}

.coin2 {
    position: relative;
    top: -50vh;
    left: 25vw;
    width: 30px;
    filter: blur(2px);
    animation-name: floating, blur;
    animation-delay: 0;
    animation-timing-function: ease-in-out;
    animation-duration: 6s;
    animation-iteration-count: infinite;
}

.coin3 {
    position: relative;
    top: -77vh;
    left: 10vw;
    width: 70px;

    animation-name: floating, blur;
    animation-delay: 0;
    animation-timing-function: ease-in-out;
    animation-duration: 5s;
    animation-iteration-count: infinite;
}

.coin4 {
    position: relative;
    top: -60vh;
    left: 30vw;
    width: 50px;
    animation-name: floating, blur;
    animation-delay: 0;
    animation-timing-function: ease-in-out;
    animation-duration: 4s;
    animation-iteration-count: infinite;
}

.coin5 {
    position: relative;
    top: -40vh;
    left: -30vw;
    width: 30px;
    // filter: blur(2px);
    animation-name: floating, blur;
    animation-delay: 0;
    animation-timing-function: ease-in-out;
    animation-duration: 6s;
    animation-iteration-count: infinite;
}

.coin6 {
    position: relative;
    top: -80vh;
    left: -30vw;
    width: 50px;
    transform: rotate(70deg);
    animation-name: floating, blur;
    animation-delay: 0;
    animation-timing-function: ease-in-out;
    animation-duration: 7s;
    animation-iteration-count: infinite;
}


@keyframes blur {
    0% {
        filter: blur(0px);
    }

    30% {
        filter: blur(0px);
    }

    50% {
        filter: blur(4px);
    }

    70% {
        filter: blur(0px);
    }

    100% {
        filter: blur(0px);
    }

}



@keyframes flipCoin {
    0% {
        transform: rotateX(0deg);
    }

    50% {
        transform: rotateX(180deg);
    }

    100% {
        transform: rotateX(0deg);
    }
}



@keyframes floating {
    0% {
        transform: translateY(0px);
    }

    50% {
        transform: translateY(-30px);
    }

    100% {
        transform: translateY(0px);
    }
}

@keyframes shake {
    0% {
        transform: translate(1px, 1px) rotate(0deg);
    }

    10% {
        transform: translate(-1px, -2px) rotate(-1deg);
    }

    20% {
        transform: translate(-3px, 0px) rotate(1deg);
    }

    30% {
        transform: translate(3px, 2px) rotate(0deg);
    }

    40% {
        transform: translate(1px, -1px) rotate(1deg);
    }

    50% {
        transform: translate(-1px, 2px) rotate(-1deg);
    }

    60% {
        transform: translate(-3px, 1px) rotate(0deg);
    }

    70% {
        transform: translate(3px, 1px) rotate(-1deg);
    }

    80% {
        transform: translate(-1px, -1px) rotate(1deg);
    }

    90% {
        transform: translate(1px, 2px) rotate(0deg);
    }

    100% {
        transform: translate(1px, -2px) rotate(-1deg);
    }
}



